* {
  box-sizing: border-box;
}

body {
  color: #000;
  background-color: #fff;
  padding-top: 60px;
  font-size: 14px;
  font-weight: normal;
  line-height: 1.5;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  display: block;
}

.search-form {
  width: 100%;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 10px;
  display: flex;
  position: fixed;
  top: 0;
  left: 20%;
}

button.is-hidden {
  display: none;
}

button {
  min-width: 200px;
  color: #00f;
  background-color: #f5e106;
  border: 1px solid rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  margin-left: 20px;
  font-size: 16px;
  font-weight: 800;
  display: flex;
}

button:hover {
  color: #000;
  background-color: #f2fa03;
  border: 1px solid #00f;
}

.input-search {
  width: 50%;
  height: 40px;
  border-radius: 4px;
  margin-right: 20px;
  padding: 2px 10px;
}

.gallery {
  grid-gap: 20px;
  max-width: calc(100% - 50px);
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin: 0 auto 30px;
  padding: 0;
  display: grid;
}

.info {
  flex-wrap: nowrap;
  align-content: center;
  justify-content: center;
  display: flex;
}

.info-item {
  text-align: center;
}

.info-item:not(:last-child) {
  margin-right: 15px;
}

.gallery img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  display: block;
}

